import { TranslateService } from '@ngx-translate/core';
import { Utility } from './../../../services/utility';
import { SocialProvider } from './../../../providers/social/social';
import { AchievePage } from './../achieve-page';
import { Session } from './../../../services/session';
import { NativeService } from './../../../services/native-service';
import { ModalController, Events } from 'ionic-angular';
import { Component, ComponentFactoryResolver } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import calendar from 'node-calendar' 
import { ImageModalModel } from '../../common/image-modal/image-modal';
/**
 * Generated class for the SignInPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sign-in',
  templateUrl: 'sign-in.html',
})
export class SignInPage extends AchievePage{

  private isSigned:boolean=false;
  private weeksDisplay:any = []
  private monthDays: any[] = [] 
  private currentYearMonth: string = "";
  private CheckInRecordList: any[] = [];
  private totalCheckedCount : number = 0;
  isIos: boolean = false;
  isDataLoading: boolean = false;
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private modal: ModalController,
    public nativeService: NativeService,
    public session: Session,
    public socialProvider: SocialProvider,
    private translate: TranslateService,
    private events: Events) {
      
      
      super(session,nativeService) 
      let Keys = ['SOCIAL_CALENDAR_SUNDAY',
      'SOCIAL_CALENDAR_MONDAY',
      'SOCIAL_CALENDAR_TUESDAY',
      'SOCIAL_CALENDAR_WEDNESDAY',
      'SOCIAL_CALENDAR_THURSDAY',
      'SOCIAL_CALENDAR_FRIDAY',
      'SOCIAL_CALENDAR_SATURDAY']
    
      Keys.forEach(key=>{
        this.weeksDisplay.push(this.translate.instant(key))
      })
      this.currentYearMonth = Utility.dateFormat(new Date())
      this.monthDays = this.createCalendar(this.currentYearMonth)
      this.getCheckInRecord()
      this.isIos = this.nativeService.isIos();
  }

  chooseDate(currentYearMonth: string){ 
    let monthyear = new Date (currentYearMonth.substr(0,7)) 
    this.monthDays = this.createCalendar(monthyear); 
    this.getCheckInRecord(monthyear.getFullYear().toString(), (monthyear.getMonth() + 1).toString())
    
  }

  _getLastYearAndMonth(originDate: Date){
 
    let result = { year: 0, month: 0 };
    let date = new Date(originDate)  
    if(date.getMonth() == 0) {
      result.year = date.getFullYear() - 1 
      result.month = 12
    }else{ 
      result.year = date.getFullYear()
      result.month = date.getMonth() 
    }
    return new Date(`${result.year}-${result.month >= 10 ? result.month : '0' + result.month.toString() }-01` ); 
  }

  _getNextYearAndMonth(originDate: Date){ 
    let result = { year: 0, month: 0 };
    let date = new Date(originDate)  
    if(date.getMonth() == 11) {
      result.year = date.getFullYear() + 1 
      result.month = 1
    }else{ 
      result.year = date.getFullYear()
      result.month = date.getMonth() + 2 
    }
    return new Date(`${result.year}-${result.month >= 10 ? result.month : '0' + result.month.toString()}-01` ); 
  }

  createCalendar(currentYearMonth){
    let currentMonthyear = new Date(currentYearMonth) 
    let lastMonthyear = this._getLastYearAndMonth(currentMonthyear);
    let nextMonthyear = this._getNextYearAndMonth(currentMonthyear);
    let currentMonthDays = new calendar.Calendar(6).monthdayscalendar(currentMonthyear.getFullYear() , currentMonthyear.getMonth() + 1)  
    let lastMonthDays = new calendar.Calendar(6).monthdayscalendar(lastMonthyear.getFullYear() , lastMonthyear.getMonth() + 1)  
    let nextMonthDays = new calendar.Calendar(6).monthdayscalendar(nextMonthyear.getFullYear() , nextMonthyear.getMonth() + 1)  

    
    let result = []; 
    let i = 0;
    for(let weeks of currentMonthDays){
      let week = [];
      let j = 0;
      for(let day of weeks){
        let item = {
          day: day,
          isCurrentMonth: true,
          isToday:false
        } 
        if(day != 0){
          currentMonthyear.setDate(day)
          item['date'] = Utility.dateFormat(currentMonthyear);
          item['isToday'] = Utility.dateFormat(currentMonthyear) == Utility.dateFormat(new Date());
          item
        } else if(i == 0){ 
          day = lastMonthDays[lastMonthDays.length - 1][j]
          item.day = day;
          lastMonthyear.setDate(day)
          item['date'] = Utility.dateFormat(lastMonthyear); 
          item.isCurrentMonth = false
        } else if(i == currentMonthDays.length -1){
          day = nextMonthDays[0][j]
          item.day = day;
          item.isCurrentMonth = false
          nextMonthyear.setDate(day)
          item['date'] = Utility.dateFormat(nextMonthyear); 
        }
        week.push(item)
        j++;
      }
      result.push(week)
      i++;
    }
    return result;
  }
 
  doSign(){
    this.currentYearMonth = Utility.dateFormat(new Date())
    this.CheckIn() 
  }


  getCheckInRecord(Year?: string, Month?: string){
    if(!Year || !Month){
      let today = new Date(); 
      Year = today.getFullYear().toString()
      Month = (today.getMonth()+ 1).toString()
    }      
    this.isDataLoading = true;                  
    this.socialProvider.CheckInRecord_New(Year,Month).subscribe(res=>{ 
      this.CheckInRecordList = res.result.checkInRecords;
      this.totalCheckedCount = res.result.continueDays;
      this.isDataLoading = false;
      for(let checkedin of res.result.checkInRecords){ 
        
        if(Utility.dateFormat(new Date(checkedin.time)) == Utility.dateFormat(new Date()) && checkedin.isChecked){ //當天是否簽到了
          this.isSigned = true;
        }
        for(let weeks of this.monthDays){
          for(let week of weeks ){ 
              if(Utility.dateFormat(new Date(checkedin.time)) == Utility.dateFormat(new Date(week.date)) ){  
                Object.assign(week,checkedin) 
              }
              
            }
          }
      } 
      // this.calcTotalCheckedCount()
    })
  }

  calcTotalCheckedCount(){
    this.totalCheckedCount = 0;
    for(let weeks of this.monthDays){
      for(let day of weeks ){ 
        if(day.isChecked){
          this.totalCheckedCount++;
        }   
      }
    }
  }
  setDateToSigned(date: string){
    for(let weeks of this.monthDays){
      for(let week of weeks ){ 
          if(date == week.date){
            week.isSigned = true
          } 
      }
    } 
  }

  CheckIn(){
    this.socialProvider.CheckIn(Utility.dateFormat(new Date()) ).subscribe(res=>{ 
      if(res.errcode == 0){ 
        this.events.publish('achievement:refresh-assets');
        this.getCheckInRecord()
        let imageModal: ImageModalModel = {
          title: this.translate.instant('SOCIAL_CHECKIN_CHECKINREWARD_TITLE'),
          subtitle: `<span style="font-size: 2.5rem;">${res.result.score}</span>${this.translate.instant('SOCIAL_IPROUD_POINTS')}`,
          imageUrl:'assets/img/achievement/sign_bg.png',
          buttonText:this.translate.instant('SOCIAL_CHECKIN_CHECKINREWARD_BUTTON_TEXT'),
          isShowCloseButton: false
        } 
        this.modal.create('ImageModalPage',{
          details:imageModal
        } ,{ 
          showBackdrop:true,
          cssClass:'sp-backdrop',
          enterAnimation: 'modal-scale-enter',
          leaveAnimation: 'modal-scale-leave'
        }).present()
        this.isSigned = true;
        // this.setDateToSigned(res.result.time)
      }
    })
  }

  
  getNextContinousDayObject(iconUrl:string){
    if(!iconUrl)return false; 
    return iconUrl.split('|')
  }
  ionViewWillEnter(){ 
    super.ionViewWillEnter()
  }
  ionViewWillLeave() {  
    super.ionViewWillLeave() 
  }
 
}



